@use '../../SPHCore/Typography';
@use '../../SPHCore/Theme';

@mixin use($ink, $hover-fill, $size) {
    .sph-navbar__item {
        width: 100%;
        height: auto;
        padding: 0.7rem;
        border-radius: 0.5rem;
        
        background: rgba(0 0 0 / 0);
        color: Theme.key-create('navbar-ink', $ink);
        
        &:hover {
            background: Theme.key-create('navbar-item-hover-fill', $hover-fill);
        }
        
        p {
            font-size: Theme.key-create('navbar-size', Typography.px-to-rem($size));
        }
        
        &.active {
            font-weight: 700;
        }
        
        @media (min-width: 570px) {
            width: max-content;
            box-shadow: none;
            background-color: rgba(0 0 0 / 0);
            border-color: rgba(0 0 0 / 0);
            
            &:hover {
                background-color: rgba(0 0 0 / 0);
                border-color: rgba(0 0 0 / 0);
                text-decoration: underline;
                text-decoration-color: var(--sph-theme--secondary);
            }
            
            p {
                width: max-content;
            }
        }
    }
}